.stars{
  display: flex;
}

.stars__star{
  width: 16px;
  height: 16px;
  background-image: url(./images/star-active.svg);
  background-repeat: no-repeat;
  background-position: 50%;
  margin: 0 0 0 4px;
}

.stars__star:first-child {
  margin: 0;
}

.stars--0 .stars__star,
.stars--1 .stars__star:not(:first-child),
.stars--2 .stars__star:nth-child(n+3),
.stars--3 .stars__star:nth-child(n+4),
.stars--4 .stars__star:nth-child(n+5),
.stars--5 .stars__star:nth-child(n+6) {
background-image: url(./images/star.svg);
}
